/* ================== Navigation =============== */

.nav_item a {
  padding-top: 1rem;
  padding-bottom: 1rem;
  transition: background-color 0.5s ease-in-out;
}
.nav_item a:hover {
  background-color: var(--primary-color) !important;
  color: white !important;
}

.nav_item .active {
  position: relative;
  font-weight: bolder;
  color: var(--primary-color) !important;
}

.nav_item .active::after {
  content: " ";
  height: 1.5rem;
  width: 1.5rem;
  background-color: var(--light-color);
  background: -webkit-linear-gradient(
    to right,
    var(--primary-color),
    var(--secondary-color),
    var(--primary-color)
  );
  background: linear-gradient(
    to right,
    var(--primary-color),
    var(--secondary-color),
    var(--primary-color)
  );
  transform: rotate(45deg);
  position: absolute;
  bottom: -0.8rem;
  left: calc(50% - 0.5rem);
  transition: background-color 0.5s ease-in-out;
}

.nav_item .active:hover::after {
  background-color: var(--primary-color);
  background: var(--primary-color);
}

@media only screen and (max-width: 766px) {
  .nav_item .active {
    border-bottom: none;
  }
  .nav_item .active::after {
    display: none;
  }
  .j_navbar_collapse {
    background-color: var(--secondary-color) !important;
    padding: 1rem;
    margin-top: 1rem;
  }
  .j_navbar_collapse li {
    box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.1);
    transform: rotateX(90deg);
    opacity: 0;
    transform-origin: top;
    animation: navAnimation 600ms ease forwards;
    background-color: white !important;
  }
  .j_navbar_collapse li a {
    padding-left: 1rem;
  }

  .j_navbar_collapse li:nth-child(2) {
    animation-delay: 200ms;
  }
  .j_navbar_collapse li:nth-child(3) {
    animation-delay: 400ms;
  }
  .j_navbar_collapse li:nth-child(4) {
    animation-delay: 600ms;
  }
  .j_navbar_collapse li:nth-child(5) {
    animation-delay: 800ms;
  }
}

@keyframes navAnimation {
  to {
    transform: rotateX(0);
    opacity: 1;
  }
}
