/*
========================================================
............... Showcase Section ======================================
========================================================
*/

.j_showcase_img_container {
  height: 90vh;
}
.j_showcase_img_container img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.j_showcase_main_text_overlay {
  background: rgba(77, 13, 10, 0.5);
  background: -webkit-linear-gradient(
    to right,
    rgba(77, 13, 10, 1),
    rgba(77, 13, 10, 0.3),
    rgba(77, 13, 10, 0),
    rgba(195, 55, 100, 0)
  );
  background: linear-gradient(
    to right,
    rgba(77, 13, 10, 1),
    rgba(77, 13, 10, 0.3),
    rgba(77, 13, 10, 0),
    rgba(195, 55, 100, 0)
  );
}

.j_carousel_drk {
  position: relative;
}
.j_carousel_drk:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background: #c33764;
  background: -webkit-linear-gradient(
    to right,
    rgba(29, 38, 113, 0.8),
    rgba(195, 55, 100, 0.8) rgb(2, 10, 82, 0.9)
  );
  background: linear-gradient(
    to right,
    rgba(77, 13, 10, 1),
    rgba(195, 55, 100, 0.2)
  );
}

@media only screen and (max-width: 740px) {
  .j_showcase_img_container {
    height: 70vh;
  }
}

/*
========================================================
............... Admission Section =====================================
========================================================
*/

.j_adm_img_container {
  height: 70vh;
}
.j_adm_img_container_left {
  height: 70vh;
}
@media only screen and (max-width: 767px) {
  .j_adm_img_container {
    height: 85vh;
  }
  .j_adm_img_container_left {
    height: 40vh;
  }
}

/*
========================================================
............... About Section =========================================
========================================================
*/

.j_img_overlay_container {
  height: 80vh;
}

.j_img_overlay_container img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 890px) {
  .j_img_overlay_container {
    height: 90vh;
  }
}
@media only screen and (max-width: 767px) {
  .j_img_overlay_container {
    height: 120vh;
  }
  .about_section_brand_container {
    position: absolute;
    width: 80%;
    top: -2rem;
    left: 10%;
    margin: 0 auto;
  }
  .j_about_us_text_container {
    position: absolute;
    top: 20%;
  }
}
@media only screen and (max-width: 420px) {
  .j_img_overlay_container {
    height: 140vh;
  }
}
