/* 
=============================================
.......... Root ============================================
=============================================
*/
:root {
  --primary-color: #4d0d0a;
  --primary-color: #4d0a0a;
  --secondary-color: rgb(228, 179, 87);
  --light-color: rgba(248, 249, 250);
  --transition: all 0.3s ease-in-out;
}

section {
  padding: 60px 0;
}

a {
  text-decoration: none;
}

/* 
=============================================
.......... Custom Classes ==================================
=============================================
*/
.j-bg-primary {
  background-color: var(--primary-color) !important;
  color: white !important;
}
.j-bg-secondary {
  background-color: var(--secondary-color) !important;
  color: white !important;
}
.j-bg-light {
  background-color: white !important;
}
.j-bg-light-gray {
  background-color: #f5f5f5 !important;
}
.j-text-primary {
  color: var(--primary-color) !important;
}

.j-text-secondary {
  color: var(--secondary-color) !important;
}

.j-text-light {
  color: white !important;
}
.j-btn-primary {
  background-color: var(--primary-color) !important;
  color: white !important;
}
.j-btn-primary:hover {
  background-color: white !important;
  border: 1px solid var(--primary-color) !important;
  font-weight: 400 !important;
  color: var(--primary-color) !important;
}

.j-btn-secondary {
  background-color: var(--secondary-color) !important;
  color: white !important;
}
.j-btn-secondary:hover {
  background-color: var(--primary-color) !important;
  color: var(--secondary-color) !important;
  border: 1px solid var(--secondary-color) !important;
}

.j-img-fluid {
  height: 100% !important;
  width: 100% !important;
  object-fit: cover !important;
}

/* 
=============================================
.......... Animations =======================================
=============================================
*/
.check-circle-container {
  margin-bottom: 1rem;
}
.check-circle-container i {
  animation: jIconAni 5s infinite;
}

@keyframes jIconAni {
  10% {
    color: var(--secondary-color);
  }
  30% {
    color: var(--primary-color);
  }
  50% {
    color: rgba(0, 255, 8, 0.821);
  }
  70% {
    color: rgb(0, 115, 128);
  }
  90% {
    color: rgb(255, 111, 0);
  }
}

.check-circle-container:hover {
  color: var(--secondary-color);
}
.check-circle-container:hover i {
  color: red;
}

.j-img-ani-zoom-in {
  animation: imgAniZoomIn 12s infinite;
}
.j-img-ani-zoom-out {
  animation: imgAniZoomOut 12s infinite;
}

@keyframes imgAniZoomIn {
  0% {
    transform: scale(1);
  }
  90% {
    transform: scale(1.4);
  }
}

@keyframes imgAniZoomOut {
  0% {
    transform: scale(1.4);
  }
  90% {
    transform: scale(1);
  }
}

/* 
=============================================
.......... Mobile Nav ======================================
=============================================
*/
.j-mobile-nav-text {
  font-size: 0.7rem;
}
.j-mobile-nav-icon {
  font-size: 1rem;
}

/* End => {Mobile Nav} */

/* 
=============================================
.......... Jump To Top ======================================
=============================================
*/

.chat-on-whatsapp,
.back-to-top {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  right: 15px;
  bottom: 15px;
  z-index: 1000;
  background-color: var(--primary-color);
  color: var(--secondary-color);
  width: 40px;
  height: 40px;
  border-radius: 50px;
  border: 2px solid var(--secondary-color);
  transition: all 0.4s;
}

.chat-on-whatsapp {
  width: 50px;
  height: 50px;
  bottom: 120px;
  padding: 2rem;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
}
.chat-on-whatsapp:hover i {
  color: var(--secondary-color);
  transform: rotate(-600deg);
  transition: var(--transition);
}
@media only screen and (max-width: 769px) {
  .back-to-top {
    bottom: 85px;
  }
  .chat-on-whatsapp {
    bottom: 200px;
  }
}

.chat-on-whatsapp,
.back-to-top i {
  font-size: 28px;
  color: #fff;
  line-height: 0;
}

.chat-on-whatsapp,
.back-to-top:hover {
  color: #fff;
}

.chat-on-whatsapp.active,
.back-to-top.active {
  visibility: visible;
  opacity: 1;
}
/* End => {Jump to top and whatsapp} */

