.j-card-2 {
  background-color: var(--secondary-color);
  cursor: default;
  border-top-right-radius: 50px;
  border-bottom-left-radius: 50px;
  position: relative;
  transition: var(--transition);
  border: 1px solid transparent;
}
.j-card-2:hover {
  background-color: var(--primary-color);
  border: 1px solid rgb(236, 234, 234);
  color: white;
}
.j-card-2 .j-highlight-submit-text {
  color: var(--primary-color);
}
.j-card-2:hover .j-highlight-submit-text {
  color: var(--secondary-color);
}
.j-card-2 .j-icon {
  background-color: var(--primary-color);
  color: #fff;
  padding: 12px;
  border-radius: 10px;
  margin-right: 1rem;
  font-size: 1rem;
  position: absolute;
  left: 2rem;
  top: -1rem;
}
.j-card-2:hover .j-icon {
  background-color: var(--secondary-color);
  border: transparent;
  color: #1f1f1f;
}
