/* Footer  */
#footer-section .nav-item .nav-link {
  color: #999999;
  color: #8c99a7;
}

#footer-section .nav-item .nav-link:hover {
  color: var(--secondary-color);
  text-decoration: underline;
}

#credit-by-jhayphix {
  color: var(--secondary-color) !important;
  text-decoration: underline;
  /* animation: jhayphixAni 8s infinite; */
}
#credit-by-jhayphix:hover {
  color: white !important;
  text-decoration: underline;
}
@keyframes jhayphixAni {
  10% {
    color: var(--secondary-color);
  }
  40% {
    color: var(--primary-color);
  }
  50% {
    color: var(--secondary-color);
  }
  80% {
    color: green;
  }
}
