/* 
=============================================
.......... Carousel ======================================
=============================================
*/

#carousel-section {
  position: relative;
}
#carousel-section::after {
  content: " ";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

#carousel-section .j-carousel-item {
  height: 45rem;
}

.j-carousel-icons-container {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 1rem;
  border-radius: 50%;
}
.j-carousel-icons-container:hover {
  background-color: rgb(255, 52, 11, 0.7);
}
@media only screen and (max-width: 900px) {
  .j-carousel-icons-container:hover {
    background-color: rgba(0, 0, 0, 0.4);
  }
}
@media only screen and (max-width: 850px) {
  #carousel-section .j-carousel-item {
    height: 40rem;
  }
}
@media only screen and (max-width: 500px) {
  #carousel-section .j-carousel-item {
    height: 70vh;
  }
}

/* 
=============================================
.......... Card Img ======================================
=============================================
*/

#card-img-section .card-img-container {
  height: 20rem;
  position: relative;
  overflow: hidden;
}

#card-img-section .card-img-container-2 {
  height: 15rem;
  position: relative;
  overflow: hidden;
}

#card-img-section .card-img-container img,
#card-img-section .card-img-container-2 img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

#card-img-section .card-img-container:hover::after,
#card-img-section .card-img-container-2:hover::after {
  content: " ";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: crosshair;
}

#card-img-section .card-img-container:hover img,
#card-img-section .card-img-container-2:hover img {
  transform: scale(1.3) rotate(-20deg);
  transition: var(--transition);
}

/* Small screens only */
@media only screen and (max-width: 900px) {
  #card-img-section .card-img-container:hover img,
  #card-img-section .card-img-container-2:hover img {
    transform: none;
  }
}
@media only screen and (max-width: 768px) {
  #card-img-section .card-img-container {
    height: 25rem;
  }
  #card-img-section .card-img-container-2 {
    height: 25rem;
  }
}

/* 
=============================================
.......... Videos ======================================
=============================================
*/
#campus-video-section {
  background: -webkit-linear-gradient(
    to bottom,
    var(--primary-color),
    var(--secondary-color)
  );
  background: linear-gradient(
    to bottom,
    var(--primary-color),
    var(--secondary-color)
  );
}
#campus-video-section .campus-video-container {
  height: 36rem;
  object-fit: cover;
  overflow: hidden;
  border-radius: 30px;
  background-color: var(--primary-color);
}
#campus-video-section .campus-video-container video {
  background-color: var(--primary-color);
  height: 100%;
  width: 100%;
  background-color: -webkit-linear-gradient(
    to right,
    var(--primary-color),
    var(--secondary-color)
  );
  background-color: linear-gradient(
    to right,
    var(--primary-color),
    var(--secondary-color)
  );
  border-radius: 30px;
}
@media only screen and (max-width: 766px) {
  #campus-video-section .campus-video-container {
    height: 30rem;
  }
}
